import { Link } from "react-router-dom";
import Card from "../../3COMPONENTS/Card/Card";
import cl from "./RenderMovies.module.css";

const RenderMovies = ({ data }) => {
    return (
        <div className={cl.listMovies}>
            {
                data.map(movie => {
                    return (
                        <Link style={{ textDecoration: 'none', color: '#c7c7c7' }} key={movie.id + movie.title} to={`/filmPage/${movie.title}id${movie.id}`}>
                            <Card img={movie.img} title={movie.title} inSub={movie.inSub} rating={movie.rating} />
                        </Link>
                    );
                })
            }
        </div>
    );
};

export default RenderMovies;